  import Vue from 'vue'
  import Router from 'vue-router'
  import liff_search from '@/components/liff_search'

  Vue.use(Router)

  export default new Router({
    routes: [
      {
        path: '/search',
        name: 'liff_search',
        component: liff_search
      }
    ]
  })



